import axios from "axios";

const ratesModule = {
  state: {
    rates: null,
    currencys: {
      BGN: "bg",
      NZD: "nz",
      ILS: "il",
      RUB: "ru",
      CAD: "ca",
      USD: "us",
      PHP: "ph",
      CHF: "ch",
      AUD: "au",
      JPY: "jp",
      TRY: "tr",
      HKD: "hk",
      MYR: "my",
      HRK: "hr",
      CZK: "cz",
      IDR: "id",
      DKK: "dk",
      NOK: "no",
      HUF: "hu",
      GBP: "gb",
      MXN: "mx",
      THB: "th",
      ISK: "is",
      ZAR: "za",
      BRL: "br",
      SGD: "sg",
      PLN: "pl",
      INR: "in",
      KRW: "kr",
      RON: "ro",
      CNY: "cn",
      SEK: "se",
      EUR: "eu"
    }
  },
  mutations: {
    SET_RATES: (state, payload) => {
      state.rates = payload;
    }
  },
  actions: {
    fetchRates: async ({ commit }) => {
      await axios
        .get("https://api.exchangeratesapi.io/latest?base=THB")
        .then(response => {
          commit("SET_RATES", response.data.rates);
        });
    }
  },
  getters: {
    getRates: state => {
      return state.rates;
    },
    getCurrency: state => {
      return state.currencys;
    }
  }
};

export default ratesModule;
